.chat {
    color: #bd761d;
    border-top: 1px solid #ffb00073;
    background-color: #3e2d2f;
    position: fixed;
    width: 100%;
    left: 0; 
    bottom: 0;
 
}
.chat .loading {
    background-color: #000;
    width: 100%;
    height: 200px;

}
.chat .inputBox {
    width: 70%;
    padding: 4px;
    display: inline-block;
} 

.chat .input {
    display: block;
    width: 100%;
    padding: 5px 10px;
    font-size: 13px;
    line-height: 1;
    color: #bd761d;
    background-color: #0000002e;
    border: 1px solid #00000080;
}
.chat .buttonBox {
    width: 30%;
    display: inline-block;
} 
.users {
    overflow-y: scroll;
    width: 15%;
    height: 200px;
    display: inline-block;
}
.messages {
    height: 200px;
    width: 85%;
    display: inline-block;
}

.message .date {
    font-size: 11px;
    text-align: center;
    margin-left: 4px;
    margin-right: 7px;
    background-color: #0000004f;
    border-radius: 3px;
    padding: 1px 5px;
}
.message .username {
    color: #e4bc73;
    font-size: 13px;
    margin-right: 5px;
}
.message .text {
    color: #e4bc73;
    font-size: 13px;
    line-height: 20px;
}
.message .text img {
    vertical-align: middle;
    height: 20px;
}
.users .online {
    text-align: center;
}
.user {
    position: relative;
}
.user .circle {
    width: 7px;
    height: 7px;
    display: inline-block;
    border-radius: 50%;
    background-color: #0000005e;
    position: absolute;
    top: 9px;
    left: 8px;

}
.user .username {
    color: #e4bc73;
    font-size: 13px;
    margin-left: 20px;
    margin-right: 5px;
}
.user .level {
    color: #e4bc73;
    font-size: 13px;
}
.topBar {
    color: #bd761d;
    border-bottom: 1px solid #ffb00073;
    background-color: #3e2d2f;
    padding: 5px;
}
.player .hp {
    position: relative;
    background-color: #0000005e;
    width: 150px;
    height: 15px;
    display: flex;
    color: white;    
    align-items: center;
    z-index: 1;
}
.player .hp > .current {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0;
    transition: 1s;
    /* background: linear-gradient(to top, #090, #0a0, #0c0, #0a0); */
    display: block;
    height: 15px;
    z-index: -1;
}
.player .hp:after {
    content: attr(data-progress) '';
    font-size: 11px;
    font-weight: bold;
    display: block;
    margin: auto;
    text-shadow: 
		-0   -1px 0   #000000,
		 0   -1px 0   #000000,
		-0    1px 0   #000000,
		 0    1px 0   #000000,
		-1px -0   0   #000000,
		 1px -0   0   #000000,
		-1px  0   0   #000000,
		 1px  0   0   #000000,
		-1px -1px 0   #000000,
		 1px -1px 0   #000000,
		-1px  1px 0   #000000,
		 1px  1px 0   #000000,
		-1px -1px 0   #000000,
		 1px -1px 0   #000000,
		-1px  1px 0   #000000,
		 1px  1px 0   #000000;
  }
